.bar-wrapper
{
    height: 80vh;
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: flex-end;
}
.bar-item
{
    margin: 0 1px;
    width: 5px;
    background-color: turquoise;
}
.div-bar{
    text-align: center;
}
.div-bar button 
{
    margin: 50px;
    padding: 20px;
    background: none;
    border: none;
    font-size: 20px;
    background-color: coral;
    border-radius: 25px;
    color:white;
    transition: all 0.5s ease-in-out;
}
.div-bar button:hover 
{
    cursor: pointer;
    transform: translateY(-10px);
    box-shadow: 1px 5px 10px 1px grey;
}